	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Nap Day","date":"24 Jun 2001","slug":"/2001-06-24-nap-day/","tags":"Y2001"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2001/06/crhsandout.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"crhsandout.jpg: \" /></p>\n<p><b>Update:</b> The sun came out this afternoon after all. Thanks, <a href=\"http://andrea.editthispage.com/2001/06/24\">Andrea</a>! But Christopher, the sand belongs <i>in</i> the sandbox!</p>\n<p>The promised summer sun for today has not arrived. MamaMaus and Christopher are celebrating the fall-like weather by both taking a mid-day nap.</p>"},"next":{"fields":{"title":"Spargel","date":"23 Jun 2001","slug":"/2001-06-23-spargel/"}},"previous":{"fields":{"title":"Miscommunication","date":"25 Jun 2001","slug":"/2001-06-25-miscommunication/"}}},{"node":{"fields":{"title":"Spargel","date":"23 Jun 2001","slug":"/2001-06-23-spargel/","tags":"Y2001"},"html":"<p><a href=\"http://www.dangerousmeta.com/posts/01/20010622\">Garret</a> linked to a NY Times story on the German <a href=\"http://www.nytimes.com/2001/06/22/world/22GERM.html\">passion</a> for white asparagus. Our neighbor <a href=\"/2001/04/14\">Achim</a> raises asparagus, selling directly at his farmplace. We can see the fields from our backyard. We met some of his Polishworkers at the Dorffest last weekend, and as I write this at 6:00am I can hear the tractor bringing them out to the fields. The season is nearly over; the Poles are going home after this weekend.</p>\n<p>It rained all day yesterday for Christopher and Oma Ruth. Christopher was in an excellent mood anyway, racking up many Grandma Brownie Points. She will be moving to nearby Buchholz this fall, they'll be able to see a lot of each other.</p>"},"next":{"fields":{"title":"Oma Ruth","date":"22 Jun 2001","slug":"/2001-06-22-oma-ruth/"}},"previous":{"fields":{"title":"Nap Day","date":"24 Jun 2001","slug":"/2001-06-24-nap-day/"}}},{"node":{"fields":{"title":"Oma Ruth","date":"22 Jun 2001","slug":"/2001-06-22-oma-ruth/","tags":"Y2001"},"html":"<p>Grandma Ruth is in Lüllau to visit Christopher. It's nice not to be tied to the day care schedule, for a couple of days at least.</p>\n<p>I got 2 Elsa AirLancer wireless (802.11b) cards on Monday, so that my Think Pad would be truly wireless. Today, 4 days later, I finally figured out the correct parameters so the card is recognized by my Linux router. I've rarely been so happy about a successful ping!</p>"},"next":{"fields":{"title":"Happy Earlyday","date":"20 Jun 2001","slug":"/2001-06-20-happy-earlyday/"}},"previous":{"fields":{"title":"Spargel","date":"23 Jun 2001","slug":"/2001-06-23-spargel/"}}}],"pathPrefix":"","first":false,"last":false,"index":995,"pageCount":1101}}

